@media all and (max-width: 1439px) {
  .container {
    max-width: 90%;
  }
  .product-details-inner-flex {
    display: grid;
    justify-content: space-between;
    grid-template-columns: 4fr 1fr;
  }
  .inner-product-details-flex {
    display: flex;
    flex-wrap: inherit;
  }
}
@media all and (max-width: 1280px) {
  .chosse-your-fvt-btn ul li a {
    padding: 10px 33px;
  }
  .shiping-system > li > img {
    width: 127px;
  }
}
@media all and (max-width: 1140px) {
  .add-to-cart > span {
    font-size: 12px;
  }
  .qty-holder2 > aside {
    font-size: 10px;
  }
  .address-info-from {
    flex: 0 0 100%;
  }
  .address-info-right-default {
    flex: 0 0 100%;
    margin-top: 25px;
  }
  .language-switch label {
    margin-right: 10px;
  }
  .footer-widget-single {
    flex: 0 0 20%;
  }
  .drop-cv-left > h3 {
    width: 83%;
  }
  .product-desc-right-content {
    flex: 0 0 54%;
  }
}

@media all and (max-width: 991px) {
  .header-main-content {
    position: relative;
  }
  .mobile-desk {
    display: block;
  }
  .menu > ul {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
  }
  .menu > ul > li > a {
    color: #fff;
  }
  .menu > ul ul {
    position: relative;
    top: 0;
    left: 0;
    background: transparent;
    padding: 0;
    padding-left: 45px;
    -webkit-transition: 0.9s;
    -o-transition: 0.9s;
    transition: 0.9s;
    display: none;
  }
  .show-menu {
    display: block;
  }
  .menu > ul ul ul.child-dropdown-inner {
    left: 0px;
    overflow: hidden;
    min-width: auto;
    max-height: 410px;
    overflow-y: scroll;
  }

  .menu > ul > li ul.child-dropdown > li > .inner-flex > i {
    font-size: 20px;
    transform: rotate(90deg);
  }

  .menu > ul > li > .inner-flex > a,
  .menu > ul > li > a {
    color: #fff !important;
  }
  .menu > ul > li ul.child-dropdown > li > .inner-flex > a,
  .menu > ul > li ul.child-dropdown > li > a {
    color: #fff !important;
  }

  .menu > ul > li ul.child-dropdown .inner-flex {
    border-bottom: 1px solid #fff;
  }
  .menu > ul > li ul.child-dropdown > li {
    border-bottom: none;
  }

  .inner-flex > i {
    color: #fff;
  }
  .inner-flex {
    display: flex;
    align-items: center;
  }

  .menu > ul ul.show-menu {
    visibility: visible;
    opacity: 1;
    -webkit-transition: 0.9s !important;
    -o-transition: 0.9s !important;
    transition: 0.9s !important;
    display: block;
  }
  .menu > ul li:hover > ul {
    visibility: inherit;
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    -webkit-transition: 0.6s;
    transition: 0.6s;
    top: 0px;
  }

  .menu > ul > li > a::after {
    display: none;
  }
  .menu > ul > li:hover a {
    color: var(--green-color);
  }
  .menu > ul > li ul.child-dropdown > li > a {
    color: #fff;
    border-bottom: 1px solid #fff;
  }

  .sidepanel {
    height: 100%;
    width: 45%;
    position: fixed;
    z-index: 1;
    top: 0;
    left: -100%;
    background-color: #111;
    overflow-x: hidden;
    overflow-y: auto;
    padding-top: 60px;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
  }
  .sidepanel a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #fff;
    display: block;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
  }

  .sidepanel a:hover {
    color: #f1f1f1;
  }

  .sidepanel .closebtn {
    position: absolute;
    top: 0;
    right: 15px;
    font-size: 36px;
    margin-left: 50px;
  }

  .openbtn {
    font-size: 22px;
    cursor: pointer;
    background-color: transparent;
    color: var(--secundery-color);
    padding: 6px 13px;
    border: none;
    position: relative;
    top: -3px;
    border-right: 2px solid var(--secundery-color);
  }
  .search-box {
    flex: 0 0 100%;
    display: inline;
    display: initial;
    position: absolute;
    width: 100%;
    top: 70px;
    margin: 0 auto;
    right: 0;
    left: 0;
  }
  .inner-search-box > input {
    padding-left: 55px;
  }
  .new-header {
    transition: all 0.5s ease-in-out;
    display: none;
  }
  .main-header-area.fade-in {
    margin-top: -8px;
  }
  .main-header-area {
    padding-bottom: 0px;
    padding-top: 5px;
  }
  #common-pading-page {
    padding-top: 132px;
  }
  .inner_offer_splide__slide {
    flex-direction: column;
  }
  .inner_offer_splide__slide > a {
    margin-right: 0px;
    margin-bottom: 10px;
  }
  .footer-widget-single.f-widget-2 {
    padding-left: 0px;
  }
  .footer-bottom-flex {
    display: grid;
    overflow: hidden;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 24px;
    align-content: center;
  }
  .product-details-inner-flex {
    grid-template-columns: 1fr;
  }
  .product-details-left {
    margin-right: 0px;
  }
  .order-detail-content .cart_summary td.cart_product {
    width: 110px;
  }
  .order-detail-content .cart_summary td.price,
  td.qty {
    width: 80px;
  }

  .order-detail-content .cart_summary td.cart_description {
    width: 120px;
  }
  .order-detail-content {
    overflow-x: auto;
    padding-bottom: 15px;
  }
  .order-detail-content .product-name > a {
    font-size: 13px;
  }
  .contact-info > span {
    font-size: 16px;
  }
  .contact-info h2,
  .get-in-tuch h2 {
    font-size: 25px;
  }
  .contact-info > ul > li > span {
    font-size: 15px;
  }
  .contact-info > ul > li > a {
    font-size: 15px;
  }
  .serial-notice > p {
    text-align: justify;
  }
  .customar-review-main-flex {
    max-width: 100%;
    width: 100%;
    box-sizing: border-box;
  }
  .drop-cv-left > h3 {
    font-size: 45px;
    width: 100%;
  }
  .shiping-system > li > img {
    width: 201px;
  }
  .our-promise-single-main {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  .related-product-main > h4 {
    padding: 13px;
  }
}

